<template>
  <div class="trade-plant container">
    <div class="head"><span @click="handleToTrade">{{ $t('translateLang.tradePlant.text2') }}</span> > {{ $t('translateLang.tradePlant.text3') }}</div>
    <el-row :gutter="30" class="min-height">
      <el-col :span="12"><new-list :plateData="plateData" v-if="plateData.trade_plate"></new-list></el-col>
      <el-col :span="12"><sell-list :plateData="plateData" v-if="plateData.trade_plate"></sell-list></el-col>
    </el-row>
  </div>
</template>

<script>
  import { getCurrencyTradeInfo } from '@/api/trade'
  import { getLinkUserInfo } from '@/utils/utils'
  import tradeHead from '../tradeDetail/components/tradeHead'
  import newList from './components/newList'
  import sellList from './components/sellList'
  export default {
    components: { tradeHead, newList, sellList },
    data() {
      return {
        trade_area_id: 0,
        currency_id: 0,
        currency_mark: '',
        currency_name: '',
        tradeDataGetInterval: null,
        tradeInfo: {},
        isLogin: true,
        ws: null,
        orderList: [],
        plateData: {}
      }
    },
    created() {
      this.init()
      this.initWebsoketLink()
      this.isLogined()
    },
    mounted() {
      document.body.className = 'bgBlack'
    },
    beforeRouteLeave(to, from, next) {
      document.body.className = ''
      clearInterval(this.tradeDataGetInterval)
      this.ws.onclose = function() {
      }
      next()
    },
    methods: {
      init() {
        this.trade_area_id = this.$route.query.trade_area_id
        this.currency_id = this.$route.query.currency_id
        this.currency_mark = this.$route.query.currency_mark
        this.currency_name = this.$route.query.currency_name
        const tradeData = {
          'trade_area_id': this.$route.query.trade_area_id,
          'currency_id': this.$route.query.currency_id,
          'currency_mark': this.$route.query.currency_mark,
          'currency_name': this.$route.query.currency_name
        }
        this.$store.commit('setTradeDetail', tradeData)
        this.handleGetCurrencyTradeInfo()
      },
      isLogined() {
        if (window.localStorage.getItem('link_userInfo')) {
          this.isLogin = true
        } else {
          this.isLogin = false
        }
      },
      handleGetCurrencyTradeInfo() {
        const data = {
          currency_trade_id: this.trade_area_id,
          currency_id: this.currency_id
        }
        const id = getLinkUserInfo('uid')
        if (id) {
          data.uid = id
        }
        getCurrencyTradeInfo(data).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.tradeInfo = { ...res.data }
        }).catch(() => {
        })
      },

      sendData() {
        const sendData = {
          'trade_area_id': this.$route.query.trade_area_id,
          'currency_id': this.$route.query.currency_id,
          'is_more_plate': 1,
          'request_type': 'plate'
        }
        this.ws.send(JSON.stringify(sendData))
      },

      toTradeDetail() {
        this.$router.push({ name: 'tradeDetail', query: { 'trade_area_id': this.$route.query.trade_area_id, 'currency_id': this.$route.query.currency_id, 'currency_mark': this.$route.query.currency_mark, 'currency_name': this.$route.query.currency_name }})
      },

      initWebsoketLink() {
        this.ws = new WebSocket(this.$store.state.tradeUrl)
        const _this = this
        this.ws.onopen = function() {
          console.log('买卖盘数据连接成功')
          _this.sendData()
          // 添加定时器，每隔一段时间向后台发送一次数据
          _this.tradeDataGetInterval = window.setInterval(function() {
            _this.sendData()
            console.log('数据已发送')
          }, 8000)
        }

        this.ws.onmessage = function(e) {
          // myChart.hideLoading()
          _this.plateData = JSON.parse(e.data).data
          console.log('----原数据--', _this.plateData)
        }
      },
      handleToTrade() {
        this.$router.push({ name: 'tradeDetail', query: { 'trade_area_id': this.$route.query.trade_area_id, 'currency_id': this.$route.query.currency_id, 'currency_mark': this.$route.query.currency_mark, 'currency_name': this.$route.query.currency_name }})
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .trade-plant {
    min-height: 700px;
    .form-container{
      background: white;
      padding: 45px 0px;
    }
    .head{
      line-height: 60px;
      width: 100%;
      background: #131625;
      color: #b0b8db;
      span{
        color: #1fa8f6;
        padding-left: 20px;
        cursor: pointer;
      }
    }
  }
</style>
<style lang="less">
.trade-plant {
  .el-col-8 {
      border-right: 1px solid #e6eaf0;
  }
  .el-col-8:last-child{
    border-right: 0px solid #ffffff;
  }
}
</style>




