<template>
  <div class="new-list">
    <div class="clearfix title">
      <p class="pull-left">{{ $t('tradePlant.part6')  }}</p> 
    </div>
      <table class="table pulbic-table">
        <thead>
          <tr>
            <td></td>
            <td>{{ $t('tradePlant.part7')  }}</td>
            <td>{{ $t('tradePlant.part8')  }}</td>
            <td>{{ $t('translateLang.tradePlant.text1') }}</td>
          </tr>
        </thead>
        <tbody  id="realtime-trade-sell">
          <tr v-for="item in reverSellData" :key="item.id" v-if="plateData.trade_plate.trade_sell || plateData.trade_plate.trade_sell.length !== 0"><td class="c-red">{{ $t('tradePlant.part10')  }}{{ item.order_id_sell }}</td><td>{{ parseFloat(item.price).toFixed(5) }}</td><td>{{ item.trade_num_sell }}</td><td>{{ item.trade_zhihe_sell }}</td></tr>
          <tr v-else><td colspan="4" valign="top"><div class="no-record zwjl"><img src="/static/images/zwjl.png"> <p>{{ $t('tradeDetail.myOrder.part13')  }}</p></div></td></tr>
        </tbody>
        
      </table>
  </div>
</template>

<script>
export default {
  props: ['plateData'],
  data() {
    return {
    }
  },
  computed: {
    reverSellData() {
      return this.plateData.trade_plate.trade_sell.reverse()
    }
  },
  methods: {
    toTradePlant() {
      this.$router.push({ name: 'tradePlant', query: { 'trade_area_id': this.$route.query.trade_area_id, 'currency_id': this.$route.query.currency_id, 'currency_mark': this.$route.query.currency_mark, 'currency_name': this.$route.query.currency_name }})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .new-list {
    margin-top: 20px;
    color: #b0b8db;
    background: #25283c;
    font-size: 12px;
    .title{
      padding: 0px 0 0 20px;
      line-height: 50px;
      background: #131625;
      color: #b0b8db;
    }
    .pulbic-table thead tr {
        // background: #2a314e;
        color: #828594;
        line-height: 1.42857143;
    }
    .pulbic-table tr td:first-child {
        padding: 0px 20px !important;
    }
    .pulbic-table tr td {
        vertical-align: middle;
        text-align: left;
        line-height: 34px;
    }
    .pulbic-table {
      text-align: left;
      background: #191b2b;
    }
    table.pulbic-table tbody tr {
      font-size: 13px;
    }
    .pulbic-table>tbody tr:hover{
      background: #2a314e;
    }
    table {
      border-spacing: 0;
      border-collapse: collapse;
      width: 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }
  }
</style>



